import React, { useEffect, useRef, useState } from 'react';
import PickItem from '@/components/PickItem';
import { useLazyQuery, useQuery } from '@apollo/client';
import { Picker, DatePicker } from 'antd-mobile';
import { GET_HISTORY, QUERY_SUBJECT, TOTAL_HISTORY } from '@/helpers/APIHelper';
import stringArrToPickArr from '@/pages/Library/utils/stringArrToPickArr';
import { moudleArr, subjectEnum } from '@/constants/globalDatas';
import getDateStr from '@/utils/getDateStr';
import {
  DateTitle,
  Info,
  InfoItem,
  InfoItemBottom,
  InfoItemTop,
  ListBox,
  ListItem,
  ListItemLeft,
  ListItemRight,
  ListItemTags,
  Page,
  SelectBox,
  SelectBoxItem,
} from './styles';

interface HistoryItemProps {
  _id: string;
  subject: string;
  timeInterval: number;
  points: number[];
  accuracy: number;
  title: string;
  timestamp: number;
  sourceType: string;
}

const Record: React.FC = () => {
  const [month, setMonth] = useState(new Date());
  const [subjectArr, setSubjectArr] = useState([{ value: '', label: '' }]); // 科目数组
  const [subjectValue, setSubjectValue] = useState(['maths1']); // 科目选择的值
  const [moudleType, setMoudleType] = useState(['0']); // 设置模块
  const { data: subjectData } = useQuery(QUERY_SUBJECT, {
    variables: { sourceType: 'center' },
  }); // 查询该科目下所有题目
  const { data = {} } = useQuery(TOTAL_HISTORY);
  const [getHistory, { data: listData }] = useLazyQuery(GET_HISTORY, {
    fetchPolicy: 'cache-and-network',
  });
  const { getHistoryByFilters } = listData || {};
  const { getTotalHistory } = data;
  const {
    accuracy = 0,
    questionAmount = 0,
    trainingTime = 0,
  } = getTotalHistory || {};

  const dateTitleRef = useRef('');
  const formatDate = (): string => {
    const year = month.getFullYear();
    const prevMonth = month.getMonth() + 1;
    return `${year}-${prevMonth}`;
  };
  const timeChange = (val: Date): void => {
    setMonth(val);
  };

  const getExamTitle = (item: HistoryItemProps): string => {
    const { sourceType, subject, timestamp } = item;
    const timerDate = new Date(Number(timestamp));

    return `${sourceType}${timerDate.getFullYear()}年${
      timerDate.getMonth() + 1
    }月${subjectEnum[subject as keyof typeof subjectEnum]}`;
  };
  // 设置下拉选项
  useEffect(() => {
    const { typeSubjects } = subjectData || {};
    if (typeSubjects) {
      setSubjectArr(stringArrToPickArr(typeSubjects, subjectEnum));
    }
  }, [subjectData]);
  // 修改下拉框刷新数据
  useEffect(() => {
    const prevMonth = `${month.getFullYear()}-${month.getMonth() + 1}`;

    getHistory({
      variables: {
        subject: subjectValue[0],
        moduleType: moudleType[0],
        time: prevMonth,
      },
    });
  }, [month, subjectValue, moudleType, getHistory]);
  return (
    <Page>
      <Info>
        <InfoItem>
          <InfoItemTop>练习时长</InfoItemTop>
          <InfoItemBottom>{(trainingTime / 60).toFixed(1)}分钟 </InfoItemBottom>
        </InfoItem>
        <InfoItem>
          <InfoItemTop>共答题数</InfoItemTop>
          <InfoItemBottom>{questionAmount}道</InfoItemBottom>
        </InfoItem>
        <InfoItem>
          <InfoItemTop>平均正确率</InfoItemTop>
          <InfoItemBottom>{(accuracy * 100).toFixed(2)}%</InfoItemBottom>
        </InfoItem>
      </Info>
      <SelectBox>
        <SelectBoxItem>
          <DatePicker
            format="YYYY-MM"
            mode="month"
            onChange={(val): void => timeChange(val)}
            value={month}
          >
            <PickItem placeholder="时间" value={formatDate()} />
          </DatePicker>
        </SelectBoxItem>
        <SelectBoxItem>
          <Picker
            data={subjectArr}
            cols={1}
            value={subjectValue}
            onOk={(value: string[]): void => setSubjectValue(value)}
          >
            <PickItem placeholder="科目" />
          </Picker>
        </SelectBoxItem>
        <SelectBoxItem>
          <Picker
            data={moudleArr}
            cols={1}
            value={moudleType}
            onOk={(value: string[]): void => setMoudleType(value)}
          >
            <PickItem placeholder="模块" />
          </Picker>
        </SelectBoxItem>
      </SelectBox>
      <ListBox>
        {getHistoryByFilters?.map((item: HistoryItemProps) => {
          const {
            _id,
            subject,
            timeInterval,
            points,
            accuracy: accuracyItem,
            title,
            timestamp,
          } = item;
          const dateTitle = getDateStr(timestamp);
          let isShowDate = false;
          // 判断时间与上一条改变则改变日期展示
          if (dateTitle !== dateTitleRef.current) {
            isShowDate = true;
            dateTitleRef.current = dateTitle;
          }
          return (
            <div key={_id}>
              {isShowDate && <DateTitle>{dateTitle}</DateTitle>}
              <ListItem>
                <ListItemLeft>
                  <div className="top">
                    <ListItemTags>
                      {subjectEnum[subject as keyof typeof subjectEnum]}
                    </ListItemTags>
                    {moudleType[0] === '2' ? getExamTitle(item) : title}
                  </div>
                  <div className="bottom">
                    用时：{(timeInterval / 60).toFixed(1)}分钟
                  </div>
                </ListItemLeft>
                <ListItemRight>
                  <div className="top">
                    正确率：{(accuracyItem * 100).toFixed(2)}%
                  </div>
                  <div className="bottom">
                    得分：{points.reduce((total, num) => total + num)}
                  </div>
                </ListItemRight>
              </ListItem>
            </div>
          );
        })}
      </ListBox>
    </Page>
  );
};

export default Record;
